body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.app {
  background-color: #f5f5f5;
  color: #363636;
  border-radius: 20px;
  border: 1px solid #d9d9d9;
  margin: 20px auto;
  padding: 20px;
  width: 500px;
  text-align: center;
}

.todoList {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todoItem {
  display: flex;
  border: 1px solid #363636;
  border-radius: 4px;
  padding: 2px;
  width: 100%;
  margin: 5px;
  justify-content: space-between;
}

.todoItem button {
  background-color: #b80232;
  color: #f5f5f5;
  border: none;
  -webkit-appearance: none;
}
.todoItem button:hover {
  background-color: #fc0043;
  color: #d9d9d9;
  border: none;
  -webkit-appearance: none;
}

.inputForm button {
  background-color: #0241bf;
  border: none;
  height: 20px;
  -webkit-appearance: none;
  border-radius: 5px;
  color: #f5f5f5;
}
.inputForm button:hover {
  background-color: #0357ff;
  color: #d9d9d9;
}

.todoItem li {
  padding-left: 20px;
  width: 100px;
  list-style: none;
}

.completed li {
  text-decoration: line-through;
}
